import { html } from 'lit';
import { ifDefined } from 'lit-html/directives/if-defined.js';
import { TSlottedAccessoryTypes } from '../../../../../../docs/helpers/slotted-accessory';
import { MarketTableV2CellTemplate } from '../../market-table-v2-cell/storybook/market-table-v2-cell.templates';

export type TMarketTableV2RowTemplateArgs = Partial<HTMLMarketTableV2RowElement> & {
  // demo
  actions?: boolean;
  content?: string;
  control?: 'none' | 'checkbox' | 'toggle';
  leadingAccessory?: TSlottedAccessoryTypes;
  nowrap?: boolean;
  slot?: string;
  sortable?: boolean;
  stickyLeft?: boolean;
  stickyRight?: boolean;
};

export const MarketTableV2RowTemplate = ({
  // demo
  actions,
  content,
  control,
  leadingAccessory,
  nowrap,
  slot,
  sortable,
  stickyLeft,
  stickyRight,

  // props
  active,
  align,
  caret,
  disabled,
  dragEnabled,
  dragHandlePosition,
  dragHandleVisibility,
  footer,
  header,
  indent,
  interactive,
  selected,
  sticky,
  valign,
}: TMarketTableV2RowTemplateArgs) => html`
  <market-table-v2-row
    ?active=${active}
    align=${ifDefined(align)}
    caret=${ifDefined(caret)}
    ?disabled=${disabled}
    ?drag-enabled=${dragEnabled}
    drag-handle-position=${ifDefined(dragHandlePosition)}
    drag-handle-visibility=${ifDefined(dragHandleVisibility)}
    ?footer=${footer}
    ?header=${header}
    indent=${ifDefined(indent)}
    ?interactive=${interactive}
    selected=${ifDefined(selected)}
    slot=${ifDefined(slot)}
    sticky=${ifDefined(sticky)}
    valign=${ifDefined(valign)}
  >
    ${content?.split(',').map((text: string, i: number) => {
      return MarketTableV2CellTemplate({
        content: text,
        control: control && i === 0 ? control : 'none',
        leadingAccessory: leadingAccessory && i === 0 ? leadingAccessory : 'none',
        nowrap,
        role: header ? 'columnheader' : undefined,
        sortable,
        sticky: getCellSticky({ content, index: i, stickyLeft, stickyRight }),
      });
    })}
    ${actions
      ? header
        ? MarketTableV2CellTemplate({
            align: 'right',
            content: 'Edit',
            role: 'columnheader',
          })
        : MarketTableV2CellTemplate({
            actions: true,
            align: 'right',
          })
      : null}
  </market-table-v2-row>
`;

const getCellSticky = ({
  content,
  index,
  stickyLeft,
  stickyRight,
}: {
  content: string;
  index: number;
  stickyLeft?: boolean;
  stickyRight?: boolean;
}) => {
  if (stickyLeft && index === 0) return 'left';
  if (stickyRight && index === content.split(',').length - 1) return 'right';
  return undefined;
};
